<template>
  <section class="storehouse-icon" @click="show()">仓库</section>
</template>

<script>
export default {
  props: ['hideStorehouse', 'hideMask'],
  methods: {
    show () {
      this.hideStorehouse = !this.hideStorehouse
      this.hideMask = !this.hideMask
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  @import url(index.css);
</style>



