<template>
  <section class="storehouse dialog" :class="{ 'hide': hideStorehouse }">
    <div class="dialog-close" @click="hide()"></div>
    <header>
      <h1 class="storehouse-title">仓库</h1>
    </header>
    <div class="storehouse-r1">
      <div class="storehouse-r1c1">
        <ul class="storehouse-tab">
          <li v-for="item in data" v-text="item.name" @click="isActive = $index" :class="{'active': isActive == $index}"></li>
        </ul>
      </div>
      <div class="storehouse-r1c2">
        <ul class="storehouse-list">
          <li v-for="item in data[isActive].imgs"><div :class="item"></div></li>
        </ul>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: ['hideStorehouse', 'hideMask'],
  methods: {
    hide () {
      this.hideStorehouse = !this.hideStorehouse
      this.hideMask = !this.hideMask
    }
  },
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      isActive: 0,
      data: [
        {
          name: '水果',
          imgs: [
            'storehouse-img1',
            'storehouse-img2',
            'storehouse-img3',
            'storehouse-img4',
            'storehouse-img5',
            'storehouse-img6',
            'storehouse-img7',
            'storehouse-img8',
            'storehouse-img9',
            'storehouse-img10',
            'storehouse-img11',
            'storehouse-img12'
          ]
        }, {
          name: '蔬菜',
          imgs: [
            'storehouse-img1',
            'storehouse-img2',
            'storehouse-img3',
            'storehouse-img4'
          ]
        }, {
          name: '粮食',
          imgs: [
            'storehouse-img5',
            'storehouse-img6',
            'storehouse-img7',
            'storehouse-img8'
          ]
        }, {
          name: '其他',
          imgs: [
            'storehouse-img9',
            'storehouse-img10',
            'storehouse-img11',
            'storehouse-img12'
          ]
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  @import url(index.css);
  
</style>



