<template>
  <section class="name">
    <div class="name-sum"></div>
    <img class="name-photo" :src="data.photo" alt="">
    <div class="name-main">
      <p>{{data.area}}</p>
      <h1>{{data.name}}</h1>
      <ul class="name-star">
        <li v-for="stars in data.star"></li>
      </ul>
    </div>
    <div class="name-icon" @click="showReclaim()"></div>
  </section>
</template>

<script>
export default {
  props: ['hideReclaim', 'hideMask'],
  methods: {
    showReclaim () {
      this.hideReclaim = !this.hideReclaim
      this.hideMask = !this.hideMask
    }
  },
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      data: {
        photo: 'https://raw.githubusercontent.com/zhouwenbin/farm/gh-pages/modules/name/img/photo.png',
        name: '黑土地农场',
        area: '¥160/亩',
        star: 4
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  @import url(index.css);
</style>



